import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, PopoverController, ModalController } from 'ionic-angular';
import { DeviceModel } from '../../interfaces/usercenter.model'; 
import * as ColorPickerClasses from '../../components/colorpicker/lib/classes';

/**
 * Generated class for the SwitchrfwyPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-switchrfwy',
  templateUrl: 'switchrfwy.html',
})
export class SwitchrfwyPage {
    rgbwy:any = null;
    title: string;
    deviceinfo: DeviceModel;
    logList: Array<any> = [];
    skip: number = 0;
    color: string = "#fff";
    w: number = 0;
    y: number = 255;
    ct: number = 50;//色温
    bn: number = 50;//亮度 
    time:number = 1;
    on_off: boolean = true;
    isscroll: boolean = true;//是否还有加载的日志数据
    take: number = 10;
    disabled: boolean = false;
    intervalVal: any;
    lastrgbw: number[] = [0, 0, 0, 0];
    rgba: ColorPickerClasses.Rgba;
    @ViewChild('lightswitchcontent') lightswitchcontentElement: any;
    colorAreaHeight: number = 300;
    contentHeight: number = 300;
    subscribetion: any;
    subscribetion1: any;
    showHeaderAndFooter: boolean = true;
    callback:any;
    // public config: IColorPickerConfiguration;
    constructor(public navCtrl: NavController,
        public params: NavParams,
        // private cpService: ColorPickerService,
        public popoverCtrl: PopoverController,
        private mc: ModalController
    ) {
        this.deviceinfo = params.get("item");
        this.title = this.deviceinfo.name;
        this.rgbwy = params.get("rgbwy");
        if(this.rgbwy != null) {
            this.w = this.rgbwy.w;
            this.y = this.rgbwy.y;
            this.ct = this.rgbwy.ct;
            this.bn = this.rgbwy.bn;
            this.time = this.rgbwy.time;
        }
        else if (this.deviceinfo.latest_staus != null) {
            this.on_off = this.deviceinfo.latest_staus.on_off;
            var dls = this.deviceinfo.latest_staus;
            if (!dls.w) {
                dls.w = 0;
            }
            if (!dls.y) {
                dls.y = 0;
            }
            if (!dls.ct) {
                dls.ct = 0;
            }
            if (!dls.bn) {
                dls.bn = 0;
            }
            this.w = dls.w;
            this.y = dls.y;
            this.ct = dls.ct;
            this.bn = dls.bn;
            this.time = this.deviceinfo.device_rgb.time;
        }
        this.callback = this.params.get("callback");
    }

    ngOnInit() {
        
    }
    ngOnDestroy() {
    }
    touchstate(state: boolean) {
        this.showHeaderAndFooter = !state;
    }

    advancedsetting() {
        let modal = this.mc.create("advancedsetting_wy", { device_id: this.deviceinfo.device_id, w: parseInt(this.w + ''), y: parseInt(this.y + ''), ct: this.ct, bn: this.bn, time: this.time, unchange:true });
        modal.onDidDismiss(data => {
            if (data) {
                this.y = data.y;
                this.w = data.w;
                //色温和亮度两个值还需要调整  公式不对
                // this.color="rgb("+data.r+","+data.g+","+data.b+")";
                // this.ct = data.ct;
                // this.bn = data.bn;
                // this.y = parseInt((255 * this.ct / 100 * this.bn / 100) + "");
                // this.w = parseInt((255 * (100 - this.ct) / 100 * this.bn / 100) + "");
                this.time = data.time;
            }
        });
        modal.present();
    }
    
    public ngAfterViewInit() {
        let element = this.lightswitchcontentElement;
        let height = parseInt(element._elementRef.nativeElement.offsetHeight);
        this.colorAreaHeight = height;
        this.contentHeight = height;
    }

    onChangeTemperature(val: { bn: number, ct: number }) {
        this.ct = val.ct;
        this.bn = val.bn;
        this.y = 255 * this.ct / 100 * this.bn / 100;
        this.w = 255 * (100 - this.ct) / 100 * this.bn / 100;
    }

    ok() {
        let rgbwy = "0,0,0," + this.w + "," + this.y + "," + this.time + "," + this.bn + "," + this.ct;
        this.dismiss(rgbwy);

    }

    //关闭modal
    dismiss(data:any) {
        this.callback(data).then(()=>{
            this.navCtrl.pop();
        });
    }
}



