import { NgModule } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';
import { SwitchrfwyPage } from './switchrfwy';
import { PipesModule   } from '../../app/pipe.module';
import { ColorPickerComponentModule   } from '../../components/colorpicker/lib/color-picker.module';
@NgModule({
  declarations: [
    SwitchrfwyPage,
  ],
  imports: [
    PipesModule, 
    ColorPickerComponentModule,
    IonicPageModule.forChild(SwitchrfwyPage),
  ],
  exports: [
    SwitchrfwyPage
  ]
})
export class SwitchrfwyPageModule {}



