import { Component, OnChanges, Directive, Input, Output, ElementRef, EventEmitter, OnInit, OnDestroy, ViewChild, AfterViewInit, ChangeDetectorRef } from '@angular/core';
// import { Observable } from 'rxjs/Observable'; 
import { ColorPickerService } from './color-picker.service';
import { Rgba, Hsla, Hsva, SliderPosition, SliderDimension } from './classes';
import { trigger, state, style, transition, animate } from '@angular/core';
@Component({
    selector: '[colorPicker]',
    templateUrl: './color-picker.html',
    styles: [`
      .color-picker * {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;
    font-size: 11px;
}

.color-picker {
    cursor: default;
    width: 100%;
    height: auto;
    top: 250px;
    z-index: 1000;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.color-picker i {
    cursor: default;
    position: relative;
}

.color-picker input {
    text-align: center;
    font-size: 13px;
    height: 26px;
    -moz-appearance: textfield;
}

.color-picker input:invalid {
    box-shadow: none;
}

.color-picker input:-moz-submit-invalid {
    box-shadow: none;
}

.color-picker input:-moz-ui-invalid {
    box-shadow: none;
}

.color-picker input::-webkit-inner-spin-button, .color-picker input::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.color-picker .button-area {
    padding: 0 16px 16px 16px;
    text-align: right;
}

.color-picker .preset-area {
    padding: 4px 15px;
}

.color-picker .preset-area .preset-label {
    width: 100%;
    padding: 4px;
    font-size: 11px;
    text-align: left;
    color: #555;
}

.color-picker .preset-area .preset-color {
    cursor: pointer;
    display: inline-block;
    width: 18px;
    height: 18px;
    margin: 4px 6px 8px 6px;
    -moz-border-radius: 25%;
    -webkit-border-radius: 25%;
    border-radius: 25%;
    -khtml-border-radius: 25%;
    border: #a9a9a9 solid 1px;
}

.color-picker .arrow {
    height: 0;
    width: 0;
    border-style: solid;
    position: absolute;
    z-index: 999999;
}

.color-picker .arrow-right {
    border-width: 5px 10px;
    border-color: transparent #777 transparent transparent;
    top: 10px;
    left: -20px;
}

.color-picker .arrow-left {
    border-width: 5px 10px;
    border-color: transparent transparent transparent #777;
    top: 10px;
    left: 231px;
}

.color-picker .arrow-bottom {
    border-width: 10px 5px;
    border-color: transparent transparent #777 transparent;
    top: -20px;
    left: 10px;
}

.color-picker .arrow-top {
    border-width: 10px 5px;
    border-color: #777 transparent transparent transparent;
    left: 10px;
}

.color-picker div.cursor-sv {
    cursor: default;
    position: relative;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    border-radius: 50%;
    -khtml-border-radius: 50%;
    width: 15px;
    height: 15px;
    border: #ddd solid 1px;
}

.color-picker div.cursor {
    cursor: default;
    position: relative;
    width: 10px;
        height: 10px;
        border-top: 10px solid rgba(255, 255, 255, 0.60);
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
}

.color-picker .saturation-lightness {
    cursor: pointer;
    width: 100%;
    height: 130px;
    border: none;
    background-size: 100% 100%;
}

.color-picker .box {
    display: flex;
    padding: 4px 8px;
}

.color-picker .box .left {
    position: relative;
    padding: 16px 8px;
}

.color-picker .box .right {
    flex: 1 1 auto;
    padding: 12px 8px;
}

.color-picker .hue {
    cursor: pointer;
    width: 100%;
    height: 16px;
    border: none;
    margin-bottom: 16px;
    background-size: 100% 100%;
    background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAJYAAAAQCAYAAAD06IYnAAAABmJLR0QA/wD/AP+gvaeTAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAB3RJTUUH4AIWDwkUFWbCCAAAAFxJREFUaN7t0kEKg0AQAME2x83/n2qu5qCgD1iDhCoYdpnbQC9bbY1qVO/jvc6k3ad91s7/7F1/csgPrujuQ17BDYSFsBAWwgJhISyEBcJCWAgLhIWwEBYIi2f7Ar/1TCgFH2X9AAAAAElFTkSuQmCC");
}

.color-picker .alpha {
    cursor: pointer;
    width: 100%;
    height: 16px;
    border: none;
    background-size: 100% 100%;
    background-image: url("data:image/png;base64,");
}

.color-picker .selected-color {
    width: 40px;
    height: 40px;
    top: 16px;
    left: 8px;
    position: absolute;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    border-radius: 50%;
    -khtml-border-radius: 50%;
}

.color-picker .selected-color-background {
    width: 40px;
    height: 40px;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    border-radius: 50%;
    -khtml-border-radius: 50%;
    background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACgAAAAoCAYAAACM/rhtAAAAh0lEQVRYR+2W0QlAMQgD60zdfwOdqa8TmI/wQMr5K0I5bZLIzLOa2nt37VVVbd+dDx5obgCC3KBLwJ2ff4PnVidkf+ucIhw80HQaCLo3DMH3CRK3iFsmAWVl6hPNDwt8EvNE5q+YuEXcMgkonVM6SdyCoEvAnZ8v1Hjx817MilmxSUB5rdLJDycZgUAZUch/AAAAAElFTkSuQmCC");
}

.color-picker .type-policy {
    position: absolute;
    top: 215px;
    right: 12px;
    background-image: url("data:image/png;base64,");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 8px 16px;
    -moz-background-size: 8px 16px;
    -webkit-background-size: 8px 16px;
    -o-background-size: 8px 16px;
    width: 16px;
    height: 24px;
}

.color-picker .hsla-text, .color-picker .rgba-text {
    width: 100%;
    font-size: 11px;
    padding: 4px 8px;
}

.color-picker .hsla-text .box, .color-picker .rgba-text .box {
    padding: 0 24px 8px 8px;
}

.color-picker .hsla-text .box input, .color-picker .rgba-text .box input {
    min-width: 0;
    flex: 1;
    margin: 0;
    float: left;
    margin-right: 8px;
    border: #a9a9a9 solid 1px;
    padding: 1px;
}

.color-picker .hsla-text .box input:last-child, .color-picker .rgba-text .box input:last-child {
    margin-right: 0;
}

.color-picker .hsla-text .box div, .color-picker .rgba-text .box div {
    flex: 1 1 auto;
    text-align: center;
    color: #555;
    margin-right: 8px;
}

.color-picker .hsla-text .box div:last-child, .color-picker .rgba-text .box div:last-child {
    margin-right: 0;
}

.color-picker .hex-text {
    width: 100%;
    font-size: 11px;
    padding: 4px 8px;
}

.color-picker .hex-text .box {
    padding: 0 24px 8px 8px;
}

.color-picker .hex-text .box input {
    flex: 1 1 auto;
    border: #a9a9a9 solid 1px;
    padding: 1px;
}

.color-picker .hex-text .box div {
    flex: 1 1 auto;
    text-align: center;
    color: #555;
    float: left;
    clear: left;
}
    `],
    host: {
        '[@lightShowState]': 'true',
        '[@hueShowState]': 'true'
    },
    animations: [
        trigger('lightShowState', [
            state('true', style({
                opacity: 1
            })),
            state('false', style({
                opacity: 0
            })),
            transition('true => false', animate('1000ms ease-out')),
            transition('false => true', animate('1000ms ease-in'))
        ]),
        trigger('hueShowState', [
            state('true', style({
                opacity: 1
            })),
            state('false', style({
                opacity: 0
            })),
            transition('true => false', animate('1000ms ease-out')),
            transition('false => true', animate('1000ms ease-in'))
        ])

    ]
})
export class ColorPickerDirective implements OnInit, OnDestroy, OnChanges, AfterViewInit {
    @Input('colorPicker') colorPicker: string;
    @Output('rgbColorChange') rgbColorChange = new EventEmitter<Array<number>>(true);
    @Output('colorPickerChange') colorPickerChange = new EventEmitter<string>(true);
    @Output('cpTemperatureChange') cpTemperatureChange = new EventEmitter<any>(true);
    @Output('cpTouchChange') cpTouchChange = new EventEmitter<boolean>(true);
    @Input('cpToggle') cpToggle: boolean;
    @Input('lightBarAllHeight') lightBarAllHeight: number = 200;
    @Output('cpToggleChange') cpToggleChange = new EventEmitter<boolean>(true);
    @Input('cpPosition') cpPosition: string = 'right';
    @Input('cpPositionOffset') cpPositionOffset: string = '0%';
    @Input('cpPositionRelativeToArrow') cpPositionRelativeToArrow: boolean = false;
    @Input('cpOutputFormat') cpOutputFormat: string = 'hex';
    @Input('cpPresetLabel') cpPresetLabel: string = 'Preset colors';
    @Input('cpPresetColors') cpPresetColors: Array<string>;
    @Input('cpCancelButton') cpCancelButton: boolean = false;
    @Input('cpCancelButtonClass') cpCancelButtonClass: string = 'cp-cancel-button-class';
    @Input('cpCancelButtonText') cpCancelButtonText: string = 'Cancel';
    @Input('cpOKButton') cpOKButton: boolean = false;
    @Input('cpOKButtonClass') cpOKButtonClass: string = 'cp-ok-button-class';
    @Input('cpOKButtonText') cpOKButtonText: string = 'OK';
    @Input('cpFallbackColor') cpFallbackColor: string = '#fff';
    @Input('cpHeight') cpHeight: string = 'auto';
    @Input('bgHeight') bgHeight: string = '130';
    @Input('cpWidth') cpWidth: string = '100%';
    @Input('cpIgnoredElements') cpIgnoredElements: any = [];
    @Input('cpDialogDisplay') cpDialogDisplay: string = 'popup';
    @Input('cpSaveClickOutside') cpSaveClickOutside: boolean = true;
    @Input('cpAlphaChannel') cpAlphaChannel: string = 'hex6';
    @Input('cpType') cpType: string = "rgb";//选择器类型 rgb,rgbw,wy,rwy,rbgww,padwy
    @Input('contentHeight') contentHeight: string = 'auto';
    @Input('cpCt') cpCt: number = 0;//0-100 色温
    @Input('cpBn') cpBn: number = 0;//0-100 亮度
    @Input('cpw') cpW: number = 0;//0-100 亮度

    @Input('left') left: string = 'auto';//这里是临时加到，不然编译不过
    //private dialog: any;
    created: boolean;
    ignoreChanges: boolean = false;

    hsva: Hsva;
    rgbaText: Rgba;
    hslaText: Hsla;
    hexText: string;
    outputColor: string;
    selectedColor: string;
    alphaSliderColor: string;
    hueSliderColor: string;
    slider: SliderPosition;
    sliderDimMax: SliderDimension;
    format: number;
    show: boolean;

    position: string;
    initialColor: string;

    //private listenerMouseDown: any;
    public listenerResize: any;
    lastX: number = 0;//最新x
    lastY: number = 0;//最新y
    lastMoveX: number = 0;//最新移动x距离
    lastMoveY: number = 0;//最新移动y距离
    totalY: number = 0;//亮度總高度
    hueLeft: number = 0;//亮度部分焦點x位置
    huetop: number = 0;//兩點部分焦點y位置
    lightBarHeight: number = 0;//亮度條高度 
    lightShowState: boolean = false;// 亮度显示状态
    hueShowState: boolean = false;// 颜色条显示状态
    cpTemperatureWhitePercent: number = 0;// 色温 白色透明度
    templateYellowPosition: number = 0;// 色温 黄色条位置
    white: number = 0;//白色滑条
    whiteLeft: number = 0;//白色滑条
    yellow: number = 0;//黄色滑条 
    showWhiteBar: boolean = false;//
    //private hsvaTemp: Hsva = new Hsva(0, 0, 0, 0);//上一次或最新設置的顏色，用來控制隱藏顏色條和亮度條
    // private hideInterval: number; 


    @ViewChild('hueSlider') hueSlider: any;
    @ViewChild('alphaSlider') alphaSlider: any;
    @ViewChild('lightSlider') lightSlider: any;
    @ViewChild('temperatureSlider') temperatureSlider: any;//色温 
    @ViewChild('templateWhiteSlider') templateWhiteSlider: any;//色温
    @ViewChild('dialogPopup') dialogElement: any;
    @ViewChild('whiteSlider') whiteSlider: any;



    constructor(private el: ElementRef, private service: ColorPickerService, private ref: ChangeDetectorRef) {
        this.created = false;

    }

    ngOnChanges(changes: any): void {
        if (changes.cpToggle) {
            // if (changes.cpToggle.currentValue) 
            // if (!changes.cpToggle.currentValue && this.dialog) this.dialog.closeColorPicker();
        }
        if (changes.colorPicker) {
            if (!this.ignoreChanges) {
                if (this.cpDialogDisplay === 'inline') {
                    this.setInitialColor(changes.colorPicker.currentValue);
                }
                this.setColorFromString(changes.colorPicker.currentValue, false);

            }
            this.ignoreChanges = false;
        }
    }
    public ngAfterViewInit() {
        if (this.cpType === "wy" || this.cpType === "rwy" || this.cpType === "padwy") {

            // this.cpTemperatureWhitePercent = this.cpBn / 100;
            this.templateYellowPosition = this.cpCt / 100 * parseInt(this.temperatureSlider.nativeElement.offsetWidth);
            this.lightBarHeight = 200 * (1 - this.cpBn / 100);
            this.cpTemperatureWhitePercent = 0.5 - this.lightBarHeight / 400;
        } else {
            this.hsva = this.service.stringToHsva(this.colorPicker)
            this.slider = new SliderPosition((this.hsva.h) * this.sliderDimMax.h, this.hsva.s * this.sliderDimMax.s - 8,
                (1 - this.hsva.v) * this.sliderDimMax.v - 8, this.hsva.a * this.sliderDimMax.a - 8)
            this.lightBarHeight = 200 * (this.slider.v / parseInt(this.bgHeight));
        }
    }
    ngOnInit() {
        this.ref.markForCheck();
        let hsva = this.service.stringToHsva(this.colorPicker);
        if (hsva === null) hsva = this.service.stringToHsva(this.colorPicker, true);
        if (hsva == null) {
            hsva = this.service.stringToHsva(this.cpFallbackColor);
        }
        this.colorPickerChange.emit(this.service.outputFormat(hsva, this.cpOutputFormat, this.cpAlphaChannel === 'hex8'));

        let alphaWidth = this.alphaSlider.nativeElement.offsetWidth;
        let hueWidth = this.hueSlider.nativeElement.offsetWidth;
        if (!this.sliderDimMax) {
            this.sliderDimMax = new SliderDimension(hueWidth, this.el.nativeElement.offsetWidth, parseInt(this.bgHeight), alphaWidth);
        }
        if (this.sliderDimMax.a == 0 && this.sliderDimMax.s == 0 && this.sliderDimMax.h == 0) {
            this.sliderDimMax = new SliderDimension(343, 375, 300, 343);
        }
        if (!this.slider) {
            this.slider = new SliderPosition(0, 0, 0, 0);
        }
        if (this.cpOutputFormat === 'rgba') {
            this.format = 1;
        } else if (this.cpOutputFormat === 'hsla') {
            this.format = 2;
        } else {
            this.format = 0;
        }
        if (this.cpW !== 0) {
            this.whiteLeft = (this.cpW / 255) * this.whiteSlider.nativeElement.offsetWidth;
            this.white = this.cpW;// (val.x / this.whiteSlider.nativeElement.offsetWidth) * 255;
        }

        //    this.listenerMouseDown = (event: any) => { this.onMouseDown(event) };
        this.listenerResize = () => { this.onResize() };
        this.openDialog(this.initialColor, false);
    }

    ngOnDestroy() {
        this.ref.detach();
    }


    setInitialColor(color: any) {
        this.initialColor = color;
    }

    openDialog(color: any, emit: boolean = true) {
        this.setInitialColor(color);
        this.setColorFromString(color, emit);
        this.openColorPicker();
    }

    cancelColor() {
        this.setColorFromString(this.initialColor, true);
        if (this.cpDialogDisplay === 'popup') {
            this.colorChanged(this.initialColor, true);
            this.closeColorPicker();
        }
    }

    oKColor() {
        if (this.cpDialogDisplay === 'popup') {
            this.closeColorPicker();
        }
    }

    setColorFromString(value: string, emit: boolean = true) {

        console.log("setColorFromString");
        let hsva: Hsva;
        if (this.cpAlphaChannel === 'hex8') {
            hsva = this.service.stringToHsva(value, true);
            if (!hsva && !this.hsva) {
                hsva = this.service.stringToHsva(value, false);
            }
        } else {
            hsva = this.service.stringToHsva(value, false);
        }
        if (hsva) {
            this.hsva = hsva;
            this.update(emit);
        }
    }

    // onMouseDown(event: any) {
    //     console.log("onmousedown");
    //     if ((!this.isDescendant(this.el.nativeElement, event.target)
    //         && event.target != this.el.nativeElement &&
    //         this.cpIgnoredElements.filter((item: any) => item === event.target).length === 0) && this.cpDialogDisplay === 'popup') {
    //         if (!this.cpSaveClickOutside) {
    //             this.setColorFromString(this.initialColor, false);
    //             this.colorChanged(this.initialColor)
    //         }
    //         this.closeColorPicker();
    //     }
    // }

    openColorPicker() {
        if (!this.show) {
            //this.setDialogPosition();
            this.show = true;
            this.toggle(true);
            // document.addEventListener('mousedown', this.listenerMouseDown);
            window.addEventListener('resize', this.listenerResize);
        }
    }

    closeColorPicker() {
        if (this.show) {
            console.log("closecolorpicker");
            this.show = false;
            this.toggle(false);
            //  document.removeEventListener('mousedown', this.listenerMouseDown);
            window.removeEventListener('resize', this.listenerResize);
        }
    }

    onResize() {
        if (this.position === 'fixed') {
            //  this.setDialogPosition();
        }
    }

    setSaturation(val: { v: number, rg: number }) {
        let hsla = this.service.hsva2hsla(this.hsva);
        hsla.s = val.v / val.rg;
        this.hsva = this.service.hsla2hsva(hsla);
        this.update();
    }

    setLightness(val: { v: number, rg: number }) {
        let hsla = this.service.hsva2hsla(this.hsva);
        hsla.l = val.v / val.rg;
        this.hsva = this.service.hsla2hsva(hsla);
        this.update();
    }

    setHue(val: { v: number, rg: number }) {
        this.hsva.h = val.v / val.rg;
        this.update();
    }

    setAlpha(val: { v: number, rg: number }) {
        this.hsva.a = val.v / val.rg;
        this.update();
    }

    setR(val: { v: number, rg: number }) {
        let rgba = this.service.hsvaToRgba(this.hsva);
        rgba.r = val.v / val.rg;
        this.hsva = this.service.rgbaToHsva(rgba);
        this.update();
    }
    setG(val: { v: number, rg: number }) {
        let rgba = this.service.hsvaToRgba(this.hsva);
        rgba.g = val.v / val.rg;
        this.hsva = this.service.rgbaToHsva(rgba);
        this.update();
    }
    setB(val: { v: number, rg: number }) {
        let rgba = this.service.hsvaToRgba(this.hsva);
        rgba.b = val.v / val.rg;
        this.hsva = this.service.rgbaToHsva(rgba);
        this.update();
    }

    setTemplate(val: { x: number }) {
        this.templateYellowPosition = val.x;
        let width = parseInt(this.temperatureSlider.nativeElement.offsetWidth);
        this.yellow = (val.x / width) * 100;
        this.cpTemperatureChange.emit({ ct: this.yellow, bn: this.white });
    }
    setTemplateWhite(val: { x: number, y: number, startX: number, startY: number }) {
        console.log(val);

        this.lastX = val.x;
        this.lastY = val.y;
        let _moveX = val.x - val.startX;
        let _moveY = val.y - val.startY;
        let moveX = _moveX - this.lastMoveX;
        let movey = _moveY - this.lastMoveY;

        let width = parseInt(this.temperatureSlider.nativeElement.offsetWidth);
        console.log("moveX:" + moveX + ",movey:" + movey + ",_moveX:" + _moveX + ",_moveY:" + _moveY + ",this.lastMoveX:" + this.lastMoveY + ",this.lastMoveX:" + this.lastMoveY)
        this.templateYellowPosition = (this.templateYellowPosition + moveX / 1.3) > width ? width : (this.templateYellowPosition + moveX / 1.3);
        this.lightBarHeight = (this.lightBarHeight + movey / 2) > 200 ? 200 : (this.lightBarHeight + movey / 1.3);
        if (this.templateYellowPosition < 0) {
            this.templateYellowPosition = 0;
        }
        if (this.lightBarHeight < 0) {
            this.lightBarHeight = 0;
        }
        this.lastMoveX = _moveX;
        this.lastMoveY = _moveY;

        this.yellow = (this.templateYellowPosition / width) * 100;

        this.white = (1 - (this.lightBarHeight / 200)) * 100;
        this.cpTemperatureWhitePercent = 0.5 - this.lightBarHeight / 400;//避免全白 本身高度只有200
        console.log("cpTemperatureWhitePercent:" + this.cpTemperatureWhitePercent + ",white:" + this.lightBarHeight);
        this.cpTemperatureChange.emit({ bn: this.white < 1 ? 1 : this.white, ct: this.yellow < 1 ? 1 : this.yellow });
    }
    setW(val: { x: number }) {
        this.whiteLeft = val.x;
        this.white = (val.x / this.whiteSlider.nativeElement.offsetWidth) * 255;
        this.rgbwChanged();
    }
    toggleWhiteBar() {
        console.log("tw");
        this.showWhiteBar = !this.showWhiteBar;
        this.ref.detectChanges();
    }
    setSaturationAndBrightness(val: { s: number, v: number, rgX: number, rgY: number, x: number, y: number, startX: number, startY: number }) {

        this.lastX = val.x;
        this.lastY = val.y;
        let _moveX = val.x - val.startX;
        let _moveY = val.y - val.startY;
        console.log("startX:" + val.startX + " ,x:" + val.x + " ,moveX:" + _moveX + "starty:" + val.startY + " ,x:" + val.y + " ,moveY:" + _moveY + " sliderH:" + this.slider.h);
        if (val.s === undefined || val.v === undefined || val.x === undefined || val.y === undefined || val.startX === undefined || val.startY === undefined || _moveY === undefined || _moveX === undefined) {
            return;
        }
        let moveX = _moveX - this.lastMoveX;
        let moveY = _moveY - this.lastMoveY;
        this.lastMoveX = _moveX;
        this.lastMoveY = _moveY;
        let update = false;
        if (((isNaN(_moveY) && _moveX !== undefined) || (!isNaN(_moveX) && Math.abs(_moveX) >= Math.abs(_moveY))) && _moveX !== 0) {
            // console.log(1);
            var hueWidth = this.hueSlider.nativeElement.offsetWidth;
            moveX = moveX / 1.3;
            var target = this.slider.h + moveX;

            if (target >= hueWidth) {
                this.hsva.h = 0;
            } else if (target <= 0) {
                this.hsva.h = 0;
            } else {
                var tv = target / hueWidth;
                this.hsva.h = tv / val.rgX;
            }
            console.log(target);
            this.hueShowState = true;
            update = true;
        } else if (((isNaN(_moveX) && _moveY !== undefined) || (!isNaN(_moveY) && Math.abs(_moveX) < Math.abs(_moveY))) && _moveY !== 0) {
            this.lightShowState = true;
            this.totalY = this.totalY + moveY / 1.3;
            if (this.totalY > parseInt(this.bgHeight)) {
                this.totalY = parseInt(this.bgHeight);
                this.lightBarHeight = 200;
                this.hsva.v = 1;
            } else if (this.totalY <= 0) {
                this.totalY = 1;
                this.lightBarHeight = 1;
                this.hsva.v = 1;
            } else {
                let v = 1 - this.totalY / parseInt(this.bgHeight);
                this.hsva.v = v / val.rgY;
            }
            update = true;
        }// else { console.log(3); return; }
        // console.log(4);
        if (update) {
            this.hueLeft = val.x;
            if (this.hsva.v == 1 && this.hsva.h == 0) {
                this.hsva = new Hsva(0, 1, 1, 1);
            }
        }
        this.update();
    }
    setLightCursor(startTouch: boolean) {
        this.lightShowState = startTouch;

        if (!startTouch) {
            this.hideBar();
        } else {
            this.cpTouchChange.emit(true);
        }
    }
    settemplateYellowPosition(startTouch: boolean) {
        this.hueShowState = startTouch;
        this.lightShowState = startTouch;
        if (!startTouch) {
            this.lastMoveX = 0;
            this.lastMoveY = 0;
            this.hideBar();
        } else {
            this.cpTouchChange.emit(true);
        }
    }
    touchState(start: boolean) {
        if (!start) {
            this.hideBar()
        }
    }
    hideBar(touching: boolean = false) {
        this.lightShowState = false;
        this.hueShowState = false;
        this.showWhiteBar = false;
        this.cpTouchChange.emit(touching);
    }
    formatPolicy(): number {
        this.format = (this.format + 1) % 3;
        if (this.format === 0 && this.hsva.a < 1 && this.cpAlphaChannel === 'hex6') {
            this.format++;
        }
        return this.format;
    }

    update(emit: boolean = true) {
        console.log("update:" + emit);
        let hsla = this.service.hsva2hsla(this.hsva);
        let rgba = this.service.denormalizeRGBA(this.service.hsvaToRgba(this.hsva));
        let hueRgba = this.service.denormalizeRGBA(this.service.hsvaToRgba(new Hsva(this.hsva.h, 1, 1, 1)));

        this.hslaText = new Hsla(Math.round((hsla.h) * 360), Math.round(hsla.s * 100), Math.round(hsla.l * 100), Math.round(hsla.a * 100) / 100);
        this.rgbaText = new Rgba(rgba.r, rgba.g, rgba.b, Math.round(rgba.a * 100) / 100);
        this.hexText = this.service.hexText(rgba, this.cpAlphaChannel === 'hex8');

        this.alphaSliderColor = 'rgb(' + rgba.r + ',' + rgba.g + ',' + rgba.b + ')';
        this.hueSliderColor = 'rgb(' + hueRgba.r + ',' + hueRgba.g + ',' + hueRgba.b + ')';

        if (this.format === 0 && this.hsva.a < 1 && this.cpAlphaChannel === 'hex6') {
            this.format++;
        }

        let lastOutput = this.outputColor;
        this.outputColor = this.service.outputFormat(this.hsva, this.cpOutputFormat, this.cpAlphaChannel === 'hex8');
        console.log(lastOutput);
        console.log(this.outputColor);
        this.selectedColor = this.service.outputFormat(this.hsva, 'rgba', false);
        let hueWidth = this.hueSlider.nativeElement.offsetWidth;

        // console.log("hueWidth:" + hueWidth + ",hueHeight:" + this.hueSlider.nativeElement.offsetHeight);
        if (!this.sliderDimMax) {
            let alphaWidth = this.alphaSlider.nativeElement.offsetWidth;
            this.sliderDimMax = new SliderDimension(hueWidth, this.el.nativeElement.offsetWidth, parseInt(this.bgHeight), alphaWidth);
        }
        this.slider = new SliderPosition((this.hsva.h) * this.sliderDimMax.h, this.hsva.s * this.sliderDimMax.s - 8,
            (1 - this.hsva.v) * this.sliderDimMax.v - 8, this.hsva.a * this.sliderDimMax.a - 8)
        if (this.hueLeft == 0) {
            this.hueLeft = this.slider.s;

            this.huetop = this.slider.v;
            // this.lightBarHeight =  (200 / parseInt(this.lightSlider.nativeElement.offsetHeight)) * 200;//亮度条长度
            //最新亮度坐標
            this.totalY = this.huetop;
            // console.log("totalY:" + this.totalY);
            this.lastX = this.hueLeft;
            this.lastY = this.huetop;
            // this.lightBarHeight = 200 * (this.totalY / parseInt(this.bgHeight));
        }
        if (this.cpType !== 'wy' && this.cpType !== 'rwy' && this.cpType !== 'padwy') {
            this.lightBarHeight = 200 * (this.totalY / parseInt(this.bgHeight));
        }
        //  this.lightBarHeight = (200 / parseInt(this.lightSlider.nativeElement.offsetHeight)) * this.slider.v;
        if (emit && lastOutput !== this.outputColor) {
            this.colorChanged(this.outputColor);
        }
    }

    isDescendant(parent: any, child: any): boolean {
        let node: any = child.parentNode;
        while (node !== null) {
            if (node === parent) {
                return true;
            }
            node = node.parentNode;
        }
        return false;
    }

    onClick() {
        if (this.cpIgnoredElements.filter((item: any) => item === this.el.nativeElement).length === 0) {

        }
    }



    colorChanged(value: string, ignore: boolean = true) {
        this.ignoreChanges = ignore;
        this.colorPickerChange.emit(value);
        this.rgbwChanged();
    }


    rgbwChanged() {
        let rgbs = this.service.hsvatorgba(this.hsva);
        // this.outputColor = this.service.outputFormat(this.hsva, this.cpOutputFormat, this.cpAlphaChannel === 'hex8');
        this.rgbColorChange.emit([rgbs[0], rgbs[1], rgbs[2], Math.round(this.white)]);
    }
    changeInput(value: string) {
        this.setColorFromString(value, true);
    }

    toggle(value: boolean) {
        this.cpToggleChange.emit(value);
    }
}


@Directive({
    selector: '[text]',
    host: {
        '(input)': 'changeInput($event.target.value)'
    }
})
export class TextDirective {
    @Output('newValue') newValue = new EventEmitter<any>();
    @Input('text') text: any;
    @Input('rg') rg: number;

    changeInput(value: string) {
        if (this.rg === undefined) {
            this.newValue.emit(value);
        } else {
            let numeric = parseFloat(value)
            if (!isNaN(numeric) && numeric >= 0 && numeric <= this.rg) {
                this.newValue.emit({ v: numeric, rg: this.rg });
            }
        }
    }
}

@Directive({
    selector: '[slider]',
    host: {
        '(mousedown)': 'start($event)',
        '(touchstart)': 'start($event)'
    }
})
export class SliderDirective {
    @Output('newValue') newValue = new EventEmitter<any>();
    @Output('startTouch') startTouch = new EventEmitter<any>();
    @Input('slider') slider: string;
    @Input('rgX') rgX: number;
    @Input('rgY') rgY: number;
    @Input('rgT') rgT: number;
    @Input('rgW') rgW: number;
    private listenerMove: any;
    private listenerStop: any;
    private moveStartx: number;
    private moveStarty: number;
    //public mc: HammerManager;
    constructor(private el: ElementRef) {
        //   this.listenerMove = (event: any) => {  this.move(event);};//event.preventDefault(); };
        //     this.listenerStop = () => { this.stop() };
    }
    // public ngAfterViewInit() {
    //     this.mc = new Hammer(this.el.nativeElement, { touchAction: "compute" });
    //     this.mc.get('pinch').set({ enable: true });
    //     this.mc.get('rotate').set({ enable: true });
    //     this.mc.get('press').set({ enable: true });
    //     this.mc.get('pan').set({ direction: Hammer.DIRECTION_ALL });
    //     this.mc.get('swipe').set({ direction: Hammer.DIRECTION_VERTICAL });
    //     this.listenerMove = (event: any) => { event.preventDefault(); this.move(event); };//event.preventDefault(); };
    //     this.listenerStop = () => { this.stop() };
    // }
    setCursor(event: any) {
        let height = this.el.nativeElement.offsetHeight;
        let width = this.el.nativeElement.offsetWidth;
        let x = Math.max(0, Math.min(this.getX(event), width));
        let y = Math.max(0, Math.min(this.getY(event), height));
        if (this.rgX !== undefined && this.rgW !== undefined) {
            this.newValue.emit({ x: x });
        }
        if (this.rgX !== undefined && this.rgT !== undefined && this.rgY === undefined) {
            this.newValue.emit({ x: x, startX: this.moveStartx });
        }
        if (this.rgX !== undefined && this.rgY !== undefined && this.rgT !== undefined) {
            this.newValue.emit({ x: x, y: y, startX: this.moveStartx, startY: this.moveStarty });
        }
        if (this.rgX !== undefined && this.rgY !== undefined) {
            this.newValue.emit({ s: x / width, v: (1 - y / height), rgX: this.rgX, rgY: this.rgY, x: x, y: y, startX: this.moveStartx, startY: this.moveStarty });
        } else if (this.rgX === undefined && this.rgY !== undefined) {//ready to use vertical sliders
            this.newValue.emit({ v: y / height, rg: this.rgY, y: y, startX: this.moveStartx, startY: this.moveStarty });
        } else {
            this.newValue.emit({ v: x / width, rg: this.rgX, x: x, startX: this.moveStartx, startY: this.moveStarty });
        }
        if ((event.type && (event.type == "touchend" || event.type == "touchcancel" || event.type == "mousedown")) ||
            (event.srcEvent && (event.srcEvent.type == "touchend" || event.srcEvent.type == "touchcancel" || event.srcEvent.type == "pointerup"
                || event.srcEvent.type == "pointercancel" || event.srcEvent.type == "pointerdown" || event.srcEvent.type == "mousedown"))) {
            console.log("兵") 
            this.stop();
            console.log(event.type ? event.type : event.srcEvent.type + "cnm!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!");
        }
    }

    move(event: any) {

        this.setCursor(event);
    }

    start(event: any) {
        this.startTouch.emit(true);
        this.moveStartx = this.getX(event);
        this.moveStarty = this.getY(event);
        this.setCursor(event);
        console.log("start:" + this.moveStartx + "，starty:" + this.moveStarty);
        // this.mc.on("mousedown mousemove touchmove  hammer.input press pointermove", this.listenerMove);
        // this.mc.on("touchend  mouseup pointerup pointercancel", this.listenerStop);
    }

    stop() {
        this.startTouch.emit(false);
        //this.mc.off("mousedown mousemove touchmove  hammer.input touchend  mouseup press");

        // document.addEventListener('mousemove', this.listenerMove);
        // document.addEventListener('touchmove', this.listenerMove);
        // document.addEventListener('mouseup', this.listenerStop);
        // document.addEventListener('touchend', this.listenerStop);
        // document.addEventListener('touchcancel', this.listenerStop);

        // document.removeEventListener('mousemove', this.listenerMove);
        // document.removeEventListener('touchmove', this.listenerMove);
        // document.removeEventListener('mouseup', this.listenerStop);
        // document.removeEventListener('touchend', this.listenerStop);
        // document.addEventListener('touchcancel', this.listenerStop);
    }

    getX(event: any): number {
        console.log(event);
        if (!event.pageX && (!event.touches || !event.touches[0] || !event.touches[0].pageX)) {
            return event.center.x - this.el.nativeElement.getBoundingClientRect().left - window.pageXOffset;
        } else {
            return (event.pageX !== undefined && event.pageX !== 0 ? event.pageX : event.touches[0].pageX) - this.el.nativeElement.getBoundingClientRect().left - window.pageXOffset;
        }
    }
    getY(event: any): number {
        console.log(event);
        if (!event.pageY && (!event.touches || !event.touches[0] || !event.touches[0].pageY)) {
            return event.center.y - this.el.nativeElement.getBoundingClientRect().top - window.pageYOffset;
        } else {
            return (event.pageY !== undefined && event.pageY !== 0 ? event.pageY : event.touches[0].pageY) - this.el.nativeElement.getBoundingClientRect().top - window.pageYOffset;
        }
    }
    getStartX(event) {
        if (event.center) {
            return event.center.x;
        } else if (event.touches[0].pageX) {
            return event.touches[0].pageX;
        }
        return 0;
    }
    getStartY(event) {
        if (event.center) {
            return event.center.y;
        } else if (event.touches[0].pageY) {
            return event.touches[0].pageY;
        }
        return 0;
    }

}



